import { School_Management_System,FlexiFit } from "../../Images"
import MajorProjectDisplay from "./MajorProductDisplay"
function MajorProjects(){

    let project_names=['School Management System','FlexiFit']
    let project_links=['https://sam-ebenezer.github.io/School-Management-System-Frontend',
                        'https://sam-ebenezer.github.io/FlexiFit/?#/calorieRecipe']

    
    
    let project_description=['Creating a dynamic web app using MERN Stack to manage student records, allow students to see their records '+
    'and create accounts for parents, teachers and students using Mongodb, React js, express server and node js.'+ 
    ' The project was done by 4 team members including me for our MERN Stack Industrial Internship. The username and password to login as admin is "admin"',
    'Creating a web app that allows user to select food recipes based on calories, meal type, etc. using spoonacular API, react js and node js.'

    ]

    let images=[School_Management_System,FlexiFit]


    let project_cards=[]

    for(let i=0;i<project_links.length;i++){
        project_cards.push(<MajorProjectDisplay num={i+1} link={project_links[i]} name={project_names[i]} img={images[i]} desc={project_description[i]}/>)
    }

    return(
        <div className='m-5' style={{clear: "left"}}>

           
            
            {project_cards}
        </div>
    )




}

export default MajorProjects