import {Gmail,Github,Linkedin } from '../../Images'
import { Link } from 'react-router-dom'
function Medialinks(){
    return(
        <div className='main_page_media_links'>
                <Link to="./Contact"><img style={{ borderRadius: '50%' }} src={Gmail} alt="Gmail icon" className="icon_class  m-0 m-3"/></Link><br />
                <a target="_blank"  rel="noreferrer" href="https://www.linkedin.com/in/sam-ebenezer-1171a61b7"><img src={Linkedin} alt="Linkedin icon" className="icon_class  m-0 m-3"/></a><br />
                <a target="_blank"  rel="noreferrer" href="https://github.com/Sam-Ebenezer"><img src={Github} alt="Github icon" className="icon_class m-0 m-3"/></a><br />
        </div>
    )
}

export default Medialinks