
function MajorProjectDisplay(props){

    return(
        <div>
            <h4>{props.num} . {props.name}</h4>
            <hr />
            
            <div className="row m-5  p-5 bg-dark ">
            <a target="_blank"  rel="noreferrer" href={props.link} className="col-sm-4 align-middle">
            <div className="card " style={{width: "18rem",float:"left"}}>
                <img className="card-img-top" src={props.img} alt={props.name +" image"} style={{height: "15rem"}}/>       
            </div>
            </a>
            <p style={{ fontSize: '20px' }} className="col-sm">
            {props.desc}
            </p>
        </div>
        </div>
    )
}

export default MajorProjectDisplay