
import { useTypewriter,Cursor } from "react-simple-typewriter"

function Intro(){

    const [text] = useTypewriter({
        words: ['Software Developer','Web Developer'],
        loop:{},
        typeSpeed: 120,
        deleteSpeed: 80,
    });

    return(

        
        <div class="jumbotron vertical-center"> 
            <div class="container text-center text-white">
                <div className="display-3 font-weight-bold">Hi There!</div>
                <div className="display-1 font-weight-bold"><b>I'm Sam Ebenezer</b></div>
                <p className="display-5 font-weight-bold">
                I am a  {' '}

                <span style={{fontWeight:'normal'}}>{text}</span>
                <span style={{color:'white'}}>
                    <Cursor cursorStyle='|'/>
                </span>
                </p>
            </div>
        </div>
         
        
    
    )
}

export default Intro